<template>
  <div class="user-setting-wrapper">
    <app-header title="设置" />
    <div class="user-setting-content">
      <van-switch-cell
        v-model="onlyWifi"
        title="仅WiFi上传下载"
        active-value="1"
        inactive-value="0"
        @change="onlyWifiSwitch"
      />
      <template v-if="!isAppStoreAccount">
        <cell
          v-for="item in items"
          :key="item.name"
          :title="item.name"
          @click="toSetting(item)"
        />
      </template>

      <!-- <div class="logout mt10" @click="logout">退出登录</div> -->
    </div>
  </div>
</template>

<script>
// import AppButton from '@components/app-button';
import Cell from '@components/app-cell';
import LoginService from '@services/login-service';
import Config from '@src/config/page/setting-config';
import RouterService from '@services/router-service';
import Context from '@yl-app-jssdk/context';
import { mapState } from 'vuex';

let key = '';

export default {
  components: {
    Cell,
    // AppButton,
  },

  data() {
    return {
      onlyWifi: 'off',
      showPopup: false,
      clearing: false,
      clearFail: false,
      items: Config(),
    };
  },

  computed: {
    ...mapState({
      isAppStoreAccount: state => state.app.isAppStoreAccount,
    }),
  },

  activated() {
    this.items = Config();
    const user = Context.getUserContext();
    key = `download_data_inwifi_${user.tenantCode}_${user.account}`;
    this.init();
  },

  methods: {
    // 初始化
    init() {
      this.onlyWifi = localStorage.getItem(key);
    },

    onlyWifiSwitch(check) {
      this.onlyWifi = check;
      localStorage.setItem(key, check);
    },

    logout() {
      this.$dialog
        .confirm({
          title: '',
          message: '确定退出登录？',
        })
        .then(() => {
          return LoginService.logout();
        });
    },

    toSetting(item) {
      return RouterService.goto(item);
    },
  },
};
</script>

