import { showZhkfModlue } from '@/src/config/sub-app-white-list';

export default function settingConfig() {
  let settingConfigData = [
    {
      name: '批量下载离线数据',
      router: '/zjapp/user-batch-download',
    },
    {
      name: '自动下载内容',
      router: '/zjapp/auto-download-setting',
    },
    {
      name: '清除数据',
      confirm: {
        message:
          '清除全部已下载和在本机创建的数据，不影响已上传的数据，确定要清除吗?',
      },
      router: '/zjapp/super-data-clear',
    },
  ];
  if (showZhkfModlue()) {
    const item = {
      name: '移动验房设置',
      router: '/zhkf/user/setting',
    };
    settingConfigData.splice(2, 0, item);
  }
  return settingConfigData;
}
